import React, { Component } from 'react'
class Input extends Component {
    constructor(props){
        super(props)
    }
   
   render(){
       return(
           <div style={{margin:'20px'}}>
               <input placeholder='AddTodo' value={this.props.value} type="text" onChange={this.props.handleChange} style={{height:'23px',width:'180px'}} />
               <button onClick={this.props.addTodo} style={{height:'29px',background:'pink'}}>添加</button>
           </div>
       )
   }
}

export default Input
