import React, { Component } from 'react'

class List extends Component {
    constructor(props){
        super(props);
    }
     renderTodos=(done)=>{
        return this.props.todos.map((todo,idx)=>{
            if(done==todo.done){
                return <li>
                    <input checked={done} onClick={()=>this.props.toggle(idx)} type="checkbox"/>
                    <span>{todo.title}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                    <button onClick={()=>this.props.delTodo(idx)} style={{backgroundColor:'pink'}}>
                        删除
                    </button>
                </li>
            }
        })

    }
     render(){
        let arr1=this.props.todos.filter(item=>!item.done);
        let arr2=this.props.todos.filter(item=>item.done);
         return(
             <div style={{margin:'20px'}}>
                 <h2>正在进行&nbsp;&nbsp;&nbsp;&nbsp;<span style={{backgroundColor:'pink',borderRadius:'35px'}}>&nbsp;&nbsp;{arr1.length}&nbsp;&nbsp;</span></h2>
                <ul>
                {this.renderTodos(false)}
                </ul>
                <h2>已经完成&nbsp;&nbsp;&nbsp;&nbsp;<span  style={{backgroundColor:'pink',borderRadius:'35px'}}>&nbsp;&nbsp;{arr2.length}&nbsp;&nbsp;</span></h2>
                <ul>
                    {this.renderTodos(true)}
                </ul>
             </div>
         )
     }
}
export default List