import Input from './Input'
import List from './List'
import React,{ Component } from 'react'

class Todolist extends Component {
    constructor(props){
        super(props);
        this.state={inputValue:'',todos:[]}
    }
    handleChange=(e)=>{
        this.setState({inputValue:e.target.value})
   }
   addTodo=()=>{
    if(this.state.inputValue!=''){
    this.setState({todos:[
        {title:this.state.inputValue,
        done:false
    },...this.state.todos]
   });}else{return;}
   this.state.inputValue='';
   }
   delTodo=(idx)=>{
    this.setState({todos:this.state.todos.filter((item,index)=>index!=idx)});
   }
   toggle=(index)=>{
    const newTodos=JSON.parse(JSON.stringify(this.state.todos));
    newTodos[index].done=!newTodos[index].done;
    this.setState({
        todos:newTodos
    })
   }
   componentDidMount(){
        let todos = localStorage.getItem('todos');
        if(todos){
            this.setState({
                todos: JSON.parse(todos)
            })
        }

    }
   componentDidUpdate(){
       localStorage.setItem(
           'todos',
           JSON.stringify(this.state.todos)
       )
   }
    render(){
        return (
            <div style={{border:'2px solid pink',height:'500px',width:'450px',backgroundColor:'#F5F5F5'}}>
                <Input addTodo={this.addTodo} handleChange={this.handleChange} value={this.state.inputValue}/>
                <List delTodo={this.delTodo} toggle={this.toggle} todos={this.state.todos}/>
            </div>
        )
    }
}

export default Todolist
